:root {
  --FONT-FAMILY: "Roboto", sans-serif;
  --RED: #ff001b;
  --ORANGE: #ff6b08;
  --WHITE: #fff;
}

body {
  font-family: var(--FONT-FAMILY);
  color: var(--WHITE);
  font-size: 12px;
  font-weight: 500;
}

.MainHeader {
  background-color: #0000;
  background-image: linear-gradient(#0000008f, #0000008f), url("header_background.9905df44.jpg");
  background-position: 10% 34%;
  background-repeat: no-repeat;
  background-size: 400%;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  height: 230px;
}

.Navigation {
  justify-content: space-between;
  align-items: center;
  padding: 40px 20px;
  display: flex;
}

.LogoWraper {
  display: flex;
}

.ImgCenter {
  justify-content: center;
  display: flex;
}

.LogoTitle {
  display: none;
}

.List {
  gap: 40px;
  margin-block: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.Link {
  text-transform: uppercase;
  text-align: center;
  color: var(--WHITE);
  line-height: 14.06px;
  text-decoration: none;
  position: relative;
}

.Link:after {
  content: "";
  background-color: var(--RED);
  width: 0;
  height: 3px;
  transition: width .3s, left .3s;
  position: absolute;
  bottom: -4px;
  right: 0;
}

.Link:hover:after {
  width: 100%;
  left: 0;
}

.Current:after {
  content: "";
  background-color: var(--RED);
  width: 100%;
  height: 3px;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.ButtonsWrapper {
  justify-content: center;
  gap: 20px;
  margin-bottom: 42px;
  padding: 20px;
  display: flex;
}

.Buttons {
  border: 1px solid var(--WHITE);
  text-transform: uppercase;
  text-align: center;
  color: var(--WHITE);
  background: none;
  border-radius: 5px;
  width: 129px;
  height: 44px;
  font-weight: 500;
}

.Buttons:hover {
  border-color: var(--ORANGE);
  background: var(--ORANGE);
  cursor: pointer;
  box-shadow: 0 8px 43px #ff6b0199;
}

.SelectedButton {
  border-color: var(--ORANGE);
  background: var(--ORANGE);
  box-shadow: 0 8px 43px #ff6b0199;
}

.LibraryContener {
  padding: 20px;
}

.DemoCard {
  background-color: #f0f8ff;
  width: 280px;
  height: 438px;
}

.Footer {
  color: #545454;
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  height: 100px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16.41px;
  display: flex;
}

.FooterText {
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  width: 236px;
  display: flex;
}

@media screen and (width >= 425px) {
  .MainHeader {
    background-position: 0 29%;
    background-size: 142%;
    height: 219px;
  }

  .Navigation {
    padding: 40px 32px;
  }

  .LogoWraper {
    gap: 10px;
  }

  .LogoTitle {
    color: var(--WHITE);
    font-size: 30px;
    font-weight: 500;
    line-height: 35.16px;
    display: inline;
  }

  .ButtonsWrapper {
    gap: 32px;
    margin-bottom: 60px;
    padding: 0;
  }

  .Buttons {
    width: 152px;
  }

  .Footer {
    justify-content: center;
    align-items: center;
    height: 79px;
    display: flex;
  }

  .FooterText {
    align-items: center;
    gap: 5px;
    width: 100%;
    display: flex;
  }
}

@media screen and (width >= 769px) {
  .MainHeader {
    background-position: 0 35%;
    background-size: cover;
    height: 216px;
  }

  .ButtonsWrapper {
    gap: 16px;
  }

  .Buttons {
    width: 147px;
    height: 42px;
  }

  .Footer {
    height: 77px;
  }
}
